<template>
    <div class="lessonManage">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">课程</span>
            <span class="back"></span>
            <span class="release" @click="release">提交</span>
        </div>
        <div class="content">
            <form class="lesson">
                <li class="lessonInfo">
                    <label>课程名称</label>
                    <input type="text" placeholder="请输入课程名称" class="textArea" v-model="lessonForm.lessonName">
                </li>
                <li class="lessonInfo">
                    <label>限制人数</label>
                    <input class="textArea" type="tel" placeholder="请输入限制人数" v-model="lessonForm.lessonLimit">
                    <span class="unit" v-if="lessonForm.lessonLimit !== ''">人</span>
                </li>
                <li class="lessonInfo" style="padding-bottom: 1.5rem;">
                    <label>课程定价</label>
                    <input class="textArea" type="tel" placeholder="请输入课程价格" v-model="lessonForm.lessonPrice">
                    <div class="detail">需扣除劳务费20%，实际课程收入=课程定价*（1-20%）</div>
                    <span class="unit" v-if="lessonForm.lessonPrice !== ''">￥</span>
                </li>
                <li class="lessonInfo" style="padding-bottom: 1.5rem;">
                    <label>课程地点</label>
                    <span class="radioArea">
                      <span class="selectArea">
                          <input type="radio" class="typeRadio" value="0" name="typeRadio1" v-model="lessonForm.userGeolocation" disabled>
                          <label>定位</label>
                      </span>
                      <span class="selectArea">
                          <input type="radio" class="typeRadio" value="1" name="typeRadio1" v-model="lessonForm.userGeolocation">
                          <label>不定位</label>
                      </span>
                      <span class="notice">(目前无法开启定位)</span>
                    </span>
                    <textarea class="detail" placeholder="请填写您的上课地址" v-model="lessonForm.place"></textarea>
                </li>
                <li class="lessonInfo" style="border: none;display: inline-block;width: 100%;text-align: start;">
                  <label>课程内容</label>
                  <textarea class="lessonDescription" placeholder="请输入您的课程内容" maxlength="150" v-model="lessonForm.lessonInfo"></textarea>
                  <div class="numLimit">
                    您还可输入<span class="textOrange">{{ 150 - lessonForm.lessonInfo.length }}</span>/150字
                  </div>
                </li>
            </form>
        </div>
    </div>
</template>

<script>
import { submitNormalLesson } from '../../api/getdata'
import store from '../../vuex/store'
import Qs from 'qs'
import { Toast } from 'mint-ui'
export default {
  name: 'lessonManage',
  data () {
    return {
      lessonForm: {
        lessonName: '',
        lessonLimit: '',
        lessonPrice: '',
        lessonInfo: '',
        userGeolocation: '1',
        place: ''
      }
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    release () {
      let date = new Date().getFullYear() + '-' + store.state.lessonDate.split('/').join('-')
      let times = store.state.lessonTime
      // console.log(date)
      // console.log(times)
      // console.log(this.lessonForm)
      if (this.lessonForm.lessonName !== '' & this.lessonForm.lessonInfo !== '' & this.lessonForm.lessonPrice !== '' & this.lessonForm.place !== '' & this.lessonForm.lessonLimit !== '') {
        let data = submitNormalLesson(Qs.stringify({
          'lessonName': this.lessonForm.lessonName,
          'lessonNote': this.lessonForm.lessonInfo,
          'lessonType': 0,
          'price': this.lessonForm.lessonPrice,
          'address': this.lessonForm.place,
          'limitcnt': this.lessonForm.lessonLimit,
          'date': date,
          'times': times
        }))
        data.then(res => {
          console.log(res)
          if (res.status === '0') {
            var arr = []
            for (var i = 0; i < store.state.lessonEdit.length; i++) {
              if (store.state.lessonEdit[i].state === true) {
                arr.push(store.state.lessonEdit[i])
              }
            }
            if (arr.length === store.state.lessonEdit.length) {
              Toast({
                message: '已提交当日全部课程',
                duration: 1500
              })
              store.commit('saveNormalLesson', [])
              setTimeout(() => {
                this.$router.push('/home/mine/myLesson')
              }, 1500)
            } else {
              Toast({
                message: '提交成功',
                duration: 1000
              })
              setTimeout(() => {
                this.$router.go(-1)
              }, 1500)
            }
          } else {
            Toast({
              message: '提交失败，请重新发布',
              duration: 1500
            })
          }
        })
      } else {
        Toast({
          message: '课程信息不能为空',
          duration: 1500
        })
      }
    },
    check () {
      console.log(this.lesson.value)
    }
  }
}
</script>

<style scoped>
* {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .release {
    position: absolute;
    right: 0;
  }

  .content {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .lessonInfo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    list-style: none;
    padding: 0.42667rem 0;
    border-bottom: 2px solid #f5f5f5;
    position: relative;
  }

  .lessonInfo label {
    font-size: 0.42667rem;
    color: #333333;
  }

  .textArea {
    display: inline-block;
    border: none;
    outline: none;
    margin-left: 0.64rem;
    font-size: 0.42667rem;
    width: 6.8rem;
  }

  .radioArea {
    margin-left: 0.8rem;
    display: flex;
    align-items: center;
  }

  .typeRadio {
    width: 0.16rem;
    height: 0.16rem;
    border-radius: 50%;
    outline: none;
    background: transparent;
    z-index: 99;
  }

  .selectArea {
    position: relative;
    display: flex;
    align-items: center;
  }

  .selectArea:last-child {
    margin-left: 0.26667rem;
  }

  .typeRadio::after {
    content: '';
    position: absolute;
    display: block;
    width: 0.26667rem;
    height: 0.26667rem;
    border-radius: 50%;
    border: 1px solid #f5f5f5;
    top: 0.13333rem;
    left: -0.08rem;
  }

  .typeRadio:checked {
    background: #ffd200;
  }

  .selectArea label {
    margin-left: 0.26667rem;
  }

  .detail {
    position: absolute;
    font-size: 0.4rem;
    color: #666666;
    width: 6.8rem;
    height: 1.2rem;
    text-align: start;
    left: 2.35rem;
    top: 1.3rem;
    border: none;
    outline: none;
  }

  .lessonDescription {
    display: block;
    width: calc(100% - 0.73667rem);
    height: 4.58667rem;
    margin-top: 0.5rem;
    padding: 0.37333rem;
    background: #f5f5f5;
    border-radius: 0.2rem;
    border: none;
    outline: none;
    resize: none;
    font-size: 0.42667rem;
    color: #333333;
  }

  .lessonDescription::placeholder {
    color: #c0c0c0;
  }

  .numLimit {
    position: absolute;
    right: 0.26777rem;
    bottom: 0.61333rem;
    font-size: 0.37333rem;
    color: #c0c0c0;
  }

  .textOrange {
    color: #ff8309;
    font-size: 0.37333rem;
  }

  .notice {
    font-size: 0.37333rem;
    color: #666666;
    display: flex;
    align-items: center;
    margin-left: 0.2rem;
  }

  .unit {
    position: absolute;
    left: 5.3rem;
    font-size: 0.42667rem;
  }
</style>



